*{
    margin: 0;
    padding: 0;
}


aside{
    flex: 1;
    padding: 20px;
    max-width: 340px;
    margin-top: 80px;
    min-width: 340px;
    box-sizing: content-box;
    /* width: 100px; */
}
.tip-container{
    position: sticky;
    top:50px;
}
.tips {
    padding: 15px 30px;
    border-radius: 5px;
    margin-bottom: 20px;
    
}

.info {
    background: #e6f7ff;
    border: 1px solid #91d5ff;
}
.success{
    background-color: #f6ffed;
    border: 1px solid #b7eb8f;
}
.error{
    background-color: #fff2f0;
    border: 1px solid #ffccc7;
}
.tips ul{
    /* list-style:none */
    padding: 10px 20px;
}

.tips li{
    /* list-style:none; */
}

main {
    display: flex;
    justify-content: center;
    /* align-items: flex-start; */
    align-items: stretch;
}

.center{
    margin-top:50px;
    flex: 0 0 700px;
    min-width: 700px;
}
